import {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import cn from "classnames";
import style from "./ForgotPassword.module.css"
import {Input} from "../UI/Input/Input";
import {Button} from "../UI/Button/Button";
import {sendEmail} from "../../Models/Settings/settings.actions";
import {InputConfirmCode} from "../InputCodeConfirmChangePass/InputConfirmCode";
import {exceptions} from "../../utils/exceptions/exceptions.consts";
import {Loader} from "../Loader/Loader";
import {ButtonTypeStyle as btn} from "../UI/Button/Button.utils";
import {isFetching, resultChangingOnPass} from "../../Models/Settings/settings.selectors";
import {errorsHelper} from "../../utils/helpers/errorsHelper";
import {validateOptions} from "../RegistrationForm/RegistrationForm.utils";

export const ForgotPassword = () => {
    const [email, setEmail] = useState('')
    const fetching = useSelector(isFetching)
    const resultChangingOnPassword = useSelector(resultChangingOnPass)
    const dispatch = useDispatch()
    const sendingEmail = () => {
        dispatch(sendEmail(email))
    }
    const {
        register,
        handleSubmit,
        formState: {errors},
    } = useForm({mode: 'onBlur'})

    if (resultChangingOnPassword === exceptions.CodeWasSendingOnEmail) {
        return <InputConfirmCode/>
    }
    const errorsData = errorsHelper(errors, validateOptions, style);
    return (
        <div>
            {fetching && <Loader/>}
            <div className={style.background}>
                <div className={style.confirmationTitle}>Подтверждение почты</div>
                <div className={style.underTitle}>Как ты мой забыть свой пароль?</div>
                <form onSubmit={handleSubmit(sendingEmail)}>
                    <Input
                        validateOption={validateOptions.email}
                        register={register}
                        onChange={e => setEmail(e.target.value)}
                        name='email'
                        value={email}
                        type='text'
                        placeholder="Почта"
                        className={cn(style.input, errorsData['email'])}/>

                    {/* eslint-disable-next-line no-nested-ternary */}
                    {resultChangingOnPassword? <p className={style.errorText}>{resultChangingOnPassword}</p>: !errorsData['email'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['email']}
                    <Button
                        text="Подтвердить"
                        submit
                        typeStyle={btn.SUCCESS}
                       />
                </form>
            </div>

        </div>
    )

};

