import { useCallback, useEffect, useRef, useState } from 'react';

export const usePopup = config => {
  const [isOpen, setOpen] = useState(false);
  const popupRef = useRef();
  const targetRef = useRef();

  const openPopup = useCallback(() => setOpen(true), [config]);

  const closePopup = useCallback(() => setOpen(false), [config]);

  const targetListener = () => (isOpen ? closePopup() : openPopup());

  const clickListener = event =>
    popupRef.current &&
    !event.path.includes(popupRef.current) &&
    event.target !== targetRef.current &&
    closePopup();

  useEffect(() => {
    targetRef.current?.addEventListener('click', targetListener);

    return () => {
      targetRef.current?.removeEventListener('click', targetListener);
    };
  });

  useEffect(() => {
    document.addEventListener('click', clickListener);

    return () => {
      document.removeEventListener('click', clickListener);
    };
  });

  return { popupRef, targetRef, isOpen, openPopup, closePopup };
};
