import {useState} from "react";
import {useDispatch} from "react-redux";
import style from './Settings.module.css'

import {Button} from "../UI/Button/Button";
import {changeEmailHook, changeNickName} from "../../Models/Settings/settings.actions";
import {Input} from "../UI/Input/Input";


// eslint-disable-next-line react/prop-types
export const Settings = ({nickname, email}) => {

    const [stateFirstDisableInput, setStateFirstDisableInput] = useState(true)
    const [stateSecondDisableInput, setSecondDisableInput] = useState(true)

    const dispatch = useDispatch()
    const [nic, setNic] = useState(nickname)
    const [localEmail, setLocalEmail] = useState(email)


    const changingNickName = () => {
        // eslint-disable-next-line no-unused-expressions
        stateFirstDisableInput ? setStateFirstDisableInput(false) : setStateFirstDisableInput(true)
        if (!stateFirstDisableInput) {
            dispatch(changeNickName(nic))
        }
    }
    const changingEmail = () =>{
        // eslint-disable-next-line no-unused-expressions
        stateSecondDisableInput? setSecondDisableInput(false):setSecondDisableInput(true)
        if (!stateSecondDisableInput) {
            dispatch(changeEmailHook(localEmail))
        }
    }

    return (
        <div className={style.backgroundPd}>
            <p className={style.titlePd}>Твои данные</p>
            <div className={style.insideBC}>
                <div>
                    <p className={style.signPd}>Твое имя</p>
                    <Input
                        disabled={stateFirstDisableInput}
                        className={style.inputForSignPd}
                        value={nic}
                        onChange={e => setNic(e.target.value)}/>
                </div>
                <div>
                    <p className={style.signPd}>Твой Mail.ua:</p>
                    <Input
                        disabled={stateSecondDisableInput}
                        className={style.inputForSignPd}
                        value={localEmail}
                        onChange={e => setLocalEmail(e.target.value)}/>

                </div>
                <div>
                    <p className={style.signPd}>Твой номер телефона:</p>
                    <p className={style.inputForSignPd}>+380683635615 </p>
                </div>
            </div>
            <div className={style.insideBC}>
                <Button
                    text="Изменить"
                    type="button"
                    typeStyle='cancel'
                    onClick={changingNickName}
                    className={stateFirstDisableInput ? style.buttonEdit : style.buttonActiveEdit}/>
                <Button
                    text="Изменить"
                    type='button'
                    typeStyle='cancel'
                    onClick={changingEmail}
                    className={stateSecondDisableInput ? style.buttonEdit : style.buttonActiveEdit}/>
                <Button
                    text="Изменить"
                    type='button'
                    className={style.buttonEdit}/>
            </div>

        </div>

    );
};

