import {createSelector} from "reselect";

const userSettings = state => state.userSettings;
export const resultSendingCodeForChangingPassword = createSelector(userSettings,state=>state.resultSendingCodeForChangingPassword)
export const isFetching = createSelector(userSettings,state=>state.fetching)
export const emailToChangePassword = createSelector(userSettings,state=>state.emailToChangePassword)
export const settingsNickName = createSelector(userSettings, state=>state.nickname)
export const settingsEmail = createSelector(userSettings, state=>state.email)
export const resultChangingOnPass = createSelector(userSettings,state=>state.resultChangingOnPass)
export const successfullyChangingEmail =createSelector(userSettings,state=>state.successfullyChangingEmail)