import {
    CHANGE_NICK_NAME_ERROR,
    CHANGE_NICK_NAME_SUCCESS,
    PUT_ALL_SETTINGS,
    PUT_EMAIL_SUCCESS,
    FETCHING,
    PUTTING_EMAIL_ERRORS,
    FINISHING_FETCHING,
    SUCCESS_CHANGING_EMAIL,
    RESULT_CHANGING_ON_PASS,
    EMAIL_TO_CHANGE_PASSWORD,
    RESULT_SENDING_CODE_FOR_CHANGE_PASSWORD, RESULT_CHANGING_NEW_PASS,

} from "./settings.actions";
import {dictionaryWithTranslatedErrors} from "../../utils/dictionary/translater.errors";

const initialState = {
    errorChangingNickName: '',
    emailErrors: '',
    fetching: false,
    nickname: '',
    email: '',
    successfullyChangingEmail: false,
    resultChangingOnPass: '',
    emailToChangePassword: '',
    resultSendingCodeForChangingPassword: ''
};

export const userSettings = (state = initialState, action) => {
    switch (action.type) {
        case EMAIL_TO_CHANGE_PASSWORD:
            return {
                ...state,
                fetching: false,
                emailToChangePassword: action.payload
            }
        case FINISHING_FETCHING:
            return {
                ...state,
                fetching: false
            }
        case FETCHING:
            return {
                ...state,
                fetching: true
            }
        case PUT_ALL_SETTINGS:
            return {
                ...state,
                nickname: action.payload.nickname,
                email: action.payload.email,

            }
        case CHANGE_NICK_NAME_SUCCESS:
            return {
                ...state,
                nickname: action.payload,

            };
        case CHANGE_NICK_NAME_ERROR:
            return {
                ...state,
                errorChangingNickName: action.payload,

            }
        case PUT_EMAIL_SUCCESS:
            return {
                ...state,
                fetching: false,
                email: action.payload,

            }
        case PUTTING_EMAIL_ERRORS:
            return {
                ...state,
                fetching: false,
                emailErrors: action.payload,
            }
        case SUCCESS_CHANGING_EMAIL:
            return {
                ...state,
                fetching: false,
                successfullyChangingEmail: true
            }
        case RESULT_CHANGING_ON_PASS:
            return {
                ...state,
                fetching: false,
                resultChangingOnPass: dictionaryWithTranslatedErrors[action.payload]
            }
        case RESULT_SENDING_CODE_FOR_CHANGE_PASSWORD:
            return {
                ...state,
                fetching: false,
                resultSendingCodeForChangingPassword: dictionaryWithTranslatedErrors[action.payload]
            }
        case RESULT_CHANGING_NEW_PASS:
            return {
                ...state,
                fetching:false,
                resultChangingNewPass:dictionaryWithTranslatedErrors[action.payload]
            }
        default:
            return state
    }
}