import {all} from 'redux-saga/effects';
import watchUserAuth from '../Models/UserAuth/userAuth.saga';
import watchUserLibrariesItems from '../Models/UserLibrariesItems/UserLibrariesItems.saga'
import watchUserSettings from "../Models/Settings/settings.saga";

export default function* rootSaga() {
    yield all([watchUserAuth(), watchUserLibrariesItems(), watchUserSettings()]);
}
