import pt from 'prop-types';
/* eslint-disable */

export const TextArea = ({
  className,
  value,
  type,
  disabled,
  wrap,
  rows,
  cols,
  onChange,
  register,
  name,
  validateOption,
}) => {
  return (
    <textarea
      {...register(name, validateOption)}
      className={className}
      value={value}
      type={type}
      disabled={disabled}
      wrap={wrap}
      rows={rows}
      cols={cols}
      onChange={onChange}
    />
  );
};

TextArea.propTypes = {
  type: pt.string,
  value: pt.string,
  disabled: pt.bool,
  wrap: pt.string,
  rows: pt.string,
  cols: pt.string,
  className: pt.string,
  onChange: pt.func,
  validateOption: pt.object,
};

TextArea.defaultProps = {
  type: 'textarea',
  value: '',
  disabled: false,
  wrap: '',
  rows: '',
  cols: '',
  className: '',
  onChange: () => {},
  validateOption: {},
};
