import api from '../../Core/api';

// eslint-disable-next-line no-return-await
export const activationAccount = async data =>await api.get(`/auth/confirmationEmail/active/${data}`)
// eslint-disable-next-line no-return-await
export const loginRequest = async data => await api.post('/auth/login', data);
// eslint-disable-next-line no-return-await
export const registrationRequest = async data => await api.post('/auth/registration',data);
// eslint-disable-next-line no-return-await
export const refreshRequest = async data =>await api.post('/auth/refresh', data);
// eslint-disable-next-line 
export const logoutRequest = async () =>await api.delete('/auth/logout');
