import { createSelector } from 'reselect';


const userAuth = state => state.userAuth;

export const isAuth = createSelector(userAuth, state => state.isAuth);
export const isRegistrationError = createSelector(userAuth,state=>state.isRegistrationError)
export const isLoginError = createSelector(userAuth,state=>state.isLoginError)
export const isFetching = createSelector(userAuth, state => state.fetching);
export const isActivationAccountError = createSelector(userAuth,state=>state.activationAccountErrors)
export const isSuccessRegistration = createSelector(userAuth,state=>state.successRegistration)
