import { memo } from 'react';
import pt from 'prop-types';
import style from "./Modal.module.css";

export const Modal = memo(({ children, close }) => {
  return (
    <div
      role="presentation"
      className={style.modalBack}
      onClick={() => close(false)}
    >
    <div role="presentation" onClick={event => event.stopPropagation()}>
        {children}
    </div>
    </div>
  );
});

Modal.propTypes = {
  children: pt.element,
  close: pt.func,
};

Modal.defaultProps = {
  children: null,
  close: () => {},
};
