/* eslint-disable */
import { memo } from 'react';
import pt from 'prop-types';
import style from './RadioButton.module.css';

export const RadioButton = (
  ({ label, value, onChange, register, validateOption, disabled, name }) => {
    return (
      <label htmlFor="radio">
        <input
          {...register(name, validateOption)}
          className={style.radio}
          type="radio"
          disabled={disabled}
          checked={value}
          onChange={onChange}
        />
        {label}
      </label>
    );
  }
);

RadioButton.propTypes = {
  value: pt.bool,
  label: pt.string,
  onChange: pt.func,
  disabled: pt.bool,
  register: pt.func,
  validateOption: pt.object,
};

RadioButton.defaultProps = {
  value: false,
  label: '',
  onChange: () => {},
  register: () => {},
  disabled: false,
  validateOption: {},
};
