/* eslint-disable */
import {call, put, takeEvery} from 'redux-saga/effects';
import {
    USER_LOGIN,
    USER_REFRESH,
    USER_LOGOUT,
    USER_REGISTRATION,
    ACTIVATION_ACCOUNT,
    userLoginFetching,
    userLoginSuccess,
    userLoginError,
    userLogouted, userRegistrationError, userRegistrationSuccess, activationAccountError,
} from './userAuth.actions';
import {
    loginRequest,
    registrationRequest,
    logoutRequest,
    refreshRequest,
    activationAccount
} from './userAuth.services';
import {setCookie, deleteCookie} from '../../utils/cookie';


function* userLogin(userData) {
    try {
        yield put(userLoginFetching());
        const userInfo = yield call(loginRequest, userData.payload);
        setCookie('accessToken', userInfo.data.tokens.accessToken);
        setCookie('refreshToken', userInfo.data.tokens.refreshToken);
        yield put(userLoginSuccess());
    } catch (error) {
        yield put(userLoginError(error.response.data.message));
    }
}

function* userRefresh(userData) {
    try {
        yield put(userLoginFetching());
        const usersTokens = yield call(refreshRequest, userData.payload);
        setCookie('accessToken', usersTokens.data.tokens.accessToken);
        setCookie('refreshToken', usersTokens.data.tokens.refreshToken);
        yield put(userLoginSuccess());
    } catch (error) {
        yield put(userLoginError(error));
    }
}

function* userRegistration(userData) {
    try {
        yield put(userLoginFetching());
        const sendingDataToRegisterAccount = yield call(registrationRequest, userData.payload);
        yield put(userRegistrationSuccess())
    } catch (error) {
        yield put(userRegistrationError(error.response.data.message));

    }
}


function* userLogout() {
    try {
        yield put(userLoginFetching());
        //yield call(logoutRequest);
        deleteCookie('accessToken');
        deleteCookie('refreshToken');
        yield put(userLogouted());
    } catch (error) {
        yield put(userLoginError(error));
    }
}


function* activateAccount(secretData) {
    try {
        yield put(userLoginFetching())
        const tokens = yield call(activationAccount,secretData.payload)
        setCookie('accessToken', tokens.data.tokens.accessToken);
        setCookie('refreshToken', tokens.data.tokens.refreshToken);
        yield put(userLoginSuccess());
    } catch (error) {
        yield put(userLoginSuccess());
        yield put(activationAccountError(error.response.data.message))
    }
}
export default function* watchUserAuth() {
    yield takeEvery(USER_LOGIN, userLogin);
    yield takeEvery(USER_REFRESH, userRefresh);
    yield takeEvery(USER_REGISTRATION, userRegistration);
    yield takeEvery(USER_LOGOUT, userLogout);
    yield takeEvery(ACTIVATION_ACCOUNT, activateAccount)
}
