export const errorsHelper = (errors, validateOptions,style) => {
  const errorsData = {};

  Object.keys(errors).forEach(lable => {
    errorsData[lable] = (
      <p className={style.errorText}>
        {lable?.message ||
          validateOptions[lable]?.message ||
          validateOptions[lable]?.pattern?.message ||
          'Ошибка'}
      </p>
    );
  });

  return errorsData;
};
