import {useForm} from "react-hook-form";
import {useDispatch, useSelector} from "react-redux";

import cn from "classnames";
import {useState} from "react";
import {useNavigate} from "react-router-dom";
import {errorsHelper} from "../../utils/helpers/errorsHelper";
import {validateOptions} from "../RegistrationForm/RegistrationForm.utils";
import style from "./ResetPassword.module.css";
import {Loader} from "../Loader/Loader";
import {Input} from "../UI/Input/Input";
import {Button} from "../UI/Button/Button";
import {emailToChangePassword, isFetching} from "../../Models/Settings/settings.selectors";
import {sendNewPasswordForChangePassword} from "../../Models/Settings/settings.actions";
import {ButtonTypeStyle as btn} from "../UI/Button/Button.utils";


export const ResetPassword = () => {
    const fetching = useSelector(isFetching)
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [totalErrors, setTotalErrors] = useState('')
    const {
        register,
        handleSubmit,
        formState: {errors},
    } = useForm({mode: 'onBlur'})
    const [password, setPassword] = useState('')
    const [passwordConfirmation, setPasswordConfirmation] = useState('')
    const emailToChangingPassword = useSelector(emailToChangePassword)

    const resetPassword = () => {
        if (password !== passwordConfirmation) {
            setTotalErrors("Пароли не совпадают")
            return
        }
        dispatch(sendNewPasswordForChangePassword({email: emailToChangingPassword, newPassword: passwordConfirmation}))
        navigate('/')
    }

    const errorsData = errorsHelper(errors, validateOptions, style);
    return (
        <div className={style.background}>
            {fetching && <Loader/>}
            <p className={style.resetPassword}>Сброс пароля</p>
            <p className={style.sign}>Я не смотрю...</p>
            <form onSubmit={handleSubmit(resetPassword)}>
                <Input
                    maxLength={26}
                    validateOption={validateOptions.password}
                    register={register}
                    onChange={e => setPassword(e.target.value)}
                    name='password'
                    value={password}
                    type='password'
                    placeholder="Новый пароль"
                    className={cn(style.inputPassword,errorsData['password'])}/>
                {!errorsData['password'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['password']}
                <Input
                    maxLength={26}
                    validateOption={validateOptions.passwordConfirmation}
                    register={register}
                    onChange={e => setPasswordConfirmation(e.target.value)}
                    name='passwordConfirmation'
                    value={passwordConfirmation}
                    type='password'
                    placeholder="Подтверди пароль"
                    className={cn(style.inputPassword, errorsData['passwordConfirmation'])}/>
                {!errorsData['passwordConfirmation'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['passwordConfirmation']}
                <Button
                    typeStyle={btn.CONFIRMATION}
                    text="Восстановить"
                    submit
                />
                {totalErrors && <p className={style.errorText}>{totalErrors}</p>}
            </form>
        </div>
    );
};
