import {useDispatch, useSelector} from "react-redux";
import cn from "classnames";
import {useForm} from "react-hook-form";
import {useEffect, useState} from "react";
import style from './InputConfirmCode.module.css'
import {Input} from "../UI/Input/Input";
import {
    emailToChangePassword,
    isFetching,
    resultSendingCodeForChangingPassword
} from "../../Models/Settings/settings.selectors";
import {validateOptions} from "../RegistrationForm/RegistrationForm.utils";
import {errorsHelper} from "../../utils/helpers/errorsHelper";
import {sendCodeForChangePassword} from "../../Models/Settings/settings.actions";
import {Loader} from "../Loader/Loader";
import {exceptions} from "../../utils/exceptions/exceptions.consts";
import {ResetPassword} from "../ResetPassword/ResetPassword";

export const InputConfirmCode = () => {
    const emailToChangePass = useSelector(emailToChangePassword)
    const [code, setCode] = useState('')
    const fetching = useSelector(isFetching)
    const resultChangingOnPassword = useSelector(resultSendingCodeForChangingPassword)
    const dispatch = useDispatch()
    const {
        register,

        formState: {errors},
    } = useForm({mode: 'onChange'})


    // eslint-disable-next-line react-hooks/rules-of-hooks,consistent-return
    useEffect(()=>{
        if (Number(code)>1000) {
            dispatch(sendCodeForChangePassword({passwordConfirmationCode: Number(code), email: emailToChangePass}))
        }
    },[code])

    if (resultChangingOnPassword === exceptions.CodeSuccessConfirm) {
        return <ResetPassword/>
    }

    const errorsData = errorsHelper(errors, validateOptions, style);
    return (

        <div className={style.background}>

            {fetching && <Loader/>}
            <p className={style.identifyCode}>Идентификация кода</p>
            <p className={style.sign}>Я отправил тебе секретный код на твою почту!</p>
            <form>
                <Input
                    pattern='[0-9]*'
                    maxLength={4}
                    validateOption={validateOptions.code}
                    register={register}
                    onChange={(e) => setCode((v) => (e.target.validity.valid ? e.target.value : v))}
                    name='code'
                    value={code}
                    type='text'
                    placeholder="Введи код"
                    className={cn(style.inputCode, errorsData['code'])}/>
                <p> </p>
                {resultChangingOnPassword ? <p className={style.errorText}>{resultChangingOnPassword}</p> :
                    <p>{errorsData['code']}</p>}
            </form>
        </div>
    );
};
