export const USER_POSTS_FETCHING = 'USER_POSTS_FETCHING'
export const USER_POSTS_SUCCESS = 'USER_POSTS_SUCCESS'
export const USER_POSTS_ERROR = 'USER_POSTS_ERROR'

export const GET_USER_POSTS_REQUEST = 'GET_USER_POSTS_REQUEST'
export const CREATE_USER_POSTS_REQUEST = 'CREATE_USER_POSTS_REQUEST'
export const UPDATE_USER_POSTS_REQUEST = 'UPDATE_USER_POSTS_REQUEST'
export const DELETE_USER_POSTS_REQUEST = 'DELETE_USER_POSTS_REQUEST'



export const userPostFetching = () => ({
    type: USER_POSTS_FETCHING,
})
export const userPostSuccess = data => ({
    type: USER_POSTS_SUCCESS,
    payload: data
})
export const userPostError = error => ({
    type: USER_POSTS_ERROR,
    payload: error
})


export const getUserPostRequest = () => ({
    type: GET_USER_POSTS_REQUEST,
})
export const createUserPostRequest = data => ({
    type: CREATE_USER_POSTS_REQUEST,
    payload: data,
})
export const updateUserPostRequest = data => ({
    type: UPDATE_USER_POSTS_REQUEST,
    payload: data,
})
export const deleteUserPostRequest = data => ({
    type: DELETE_USER_POSTS_REQUEST,
    payload: data,
})
