import { memo } from 'react';
import pt from 'prop-types';
import style from "./ItemLibraryCard.module.css"

export const ItemLibraryCard = memo(({ src, onClick }) => {
  return (
    <img
      role="presentation"
      className={style.img}
      onClick={onClick}
      src={src}
      alt=""
    />
  );
});

ItemLibraryCard.propTypes = {
  src: pt.string,
  onClick: pt.func,
};

ItemLibraryCard.defaultProps = {
  src: '',
  onClick: () => {},
};
