import Resizer from 'react-image-file-resizer';

export const ImagePickerTypeStyle = {
  LABEL: 'label',
  AVATAR: 'avatar',
};

const resizeConfig = {
  maxWidth: 300,
  maxHeight: 700,
  minWidth: 300,
  minHeight: 700,
};

export const resizeFile = (file, config) => {
  const resultConfig = {
    ...resizeConfig,
    ...config,
  };
  return new Promise(resolve => {
    Resizer.imageFileResizer(
      file,
      resultConfig.maxWidth,
      resultConfig.maxHeight,
      'JPEG',
      80,
      0,
      uri => {
        resolve(uri);
      },
      'base64',
      resultConfig.minWidth,
      resultConfig.minHeight
    );
  });
};
