export const USER_LOGIN = 'USER_LOGIN';
export const USER_REFRESH = 'USER_REFRESH';
export const USER_LOGOUT = 'USER_LOGOUT';
export const USER_LOGOUTED = 'USER_LOGOUTED';
export const USER_REGISTRATION = 'USER_REGISTRATION';
export const USER_LOGIN_FETCHING = 'USER_LOGIN_FETCHING';
export const USER_LOGIN_SUCCESS = 'USER_LOGIN_SUCCESS';
export const USER_LOGIN_ERROR = 'USER_LOGIN_ERROR';
export const USER_REGISTRATION_ERROR = 'USER_REGISTRATION_ERROR'
export const USER_REGISTRATION_SUCCESS = 'USER_REGISTRATION_SUCCESS'
export const ACTIVATION_ACCOUNT = 'ACTIVATION_ACCOUNT'
export const ACTIVATION_ACCOUNT_ERROR = 'ACTIVATION_ACCOUNT_ERROR'
export const GET_CHANGED_EMAIL = 'GET_CHANGED_EMAIL'
export const userLogin = data => ({
    type: USER_LOGIN,
    payload: data,
});
export const activationAccount = data => ({
    type: ACTIVATION_ACCOUNT,
    payload: data
})
export const getChangedEmail = data => ({
    type: GET_CHANGED_EMAIL,
    payload: data
})
export const activationAccountError = data => ({
    type: ACTIVATION_ACCOUNT_ERROR,
    payload: data
})
export const userRefresh = data => ({
    type: USER_REFRESH,
    payload: data,
});

export const userLogout = () => ({
    type: USER_LOGOUT,
});

export const userLogouted = () => ({
    type: USER_LOGOUTED,
});
export const userRegistrationSuccess = () => ({
    type: USER_REGISTRATION_SUCCESS,
})
export const userRegistrationError = data => ({
    type: USER_REGISTRATION_ERROR,
    payload: data
})
export const userRegistration = data => ({
    type: USER_REGISTRATION,
    payload: data,
});

export const userLoginFetching = () => ({
    type: USER_LOGIN_FETCHING,
});

export const userLoginSuccess = () => ({
    type: USER_LOGIN_SUCCESS,
});

export const userLoginError = error => ({
    type: USER_LOGIN_ERROR,
    payload: error,
});
