import {call, put, takeEvery} from 'redux-saga/effects';
import {
    CHANGE_EMAIL,
    CHANGE_NICK_NAME,
    changeNickNameErrors,
    changeNickNameSuccess,
    emailToChangePassword,
    fetching,
    finishingFetching,
    GET_USER_SETTINGS,
    putAllSettings,
    putEmail,
    puttingEmailErrors, resultChangingNewPass,
    resultChangingOnPass,
    resultSendingCodeForChangePassword,
    SEND_CODE_FOR_CHANGE_PASSWORD,
    SEND_NEW_PASSWORD_FOR_CHANGE_PASSWORD,
    SEND_REQUEST_ON_CHANGING_PASSWORD,
    successChangingEmail
} from "./settings.actions";

import {
    sendRequestOnChangingEmail,
    getSettings,
    sendChangingNickName,
    getChangeEmail,
    requestChangeOnPass, sendingCodeForChangePassword, sendingNewPassword
} from "./settings.services";
import {GET_CHANGED_EMAIL, userLoginSuccess} from "../UserAuth/userAuth.actions";
import {setCookie} from "../../utils/cookie";


function* changeNickNames(data) {
    try {
        const newNickName = yield call(sendChangingNickName, {nickname: data.payload})
        yield put(changeNickNameSuccess(newNickName.data.nickname))
    } catch (error) {
        yield put(changeNickNameErrors, error.response.data.message)
    }
}

function* getUserSettings() {
    try {
        yield put(fetching())
        const userData = yield call(getSettings)
        yield put(putAllSettings(userData.data))
        yield put(finishingFetching())
    } catch (e) {
        yield put(changeNickNameErrors(e))
    }
}

function* changeEmail(data) {
    try {
        yield put(fetching())
        const sendingEmail = yield call(sendRequestOnChangingEmail, {email: data.payload})
        yield put(putEmail(sendingEmail))
        yield put(finishingFetching())
    } catch (e) {
        yield put(puttingEmailErrors(e))
    }
}

function* gotChangedEmail(data) {
    try {
        yield put(fetching())
        yield call(getChangeEmail, data.payload)
        yield put(successChangingEmail())
        yield put(finishingFetching())
    } catch (e) {
        yield put(puttingEmailErrors(e))
    }
}

function* sendRequestOnChangingPassword(data) {
    try {
        yield put(fetching())
        const message = yield call(requestChangeOnPass, {email: data.payload})
        yield put(emailToChangePassword(data.payload))
        yield put(resultChangingOnPass(message.data.message))
        yield put(finishingFetching())
    } catch (e) {
        yield put(resultChangingOnPass(e.response.data.message))
    }
}

function* sendCodeForChangePassword(data) {
    try {
        yield put(fetching())
        const resultSendingCode = yield call(sendingCodeForChangePassword, data.payload)
        yield put(resultSendingCodeForChangePassword(resultSendingCode.data.message))
        yield put(finishingFetching())
    } catch (e) {
        yield put(resultSendingCodeForChangePassword(e.response.data.message))
    }
}

function* sendNewPassword(data) {
    try {
        yield put(fetching())
        const resultSentNewPassword = yield call(sendingNewPassword, data.payload)
        setCookie('accessToken', resultSentNewPassword.data.tokens.accessToken);
        setCookie('refreshToken', resultSentNewPassword.data.tokens.refreshToken);
        yield put(userLoginSuccess())
        yield put(resultSendingCodeForChangePassword(''))
        yield put(emailToChangePassword(''))
        yield put(resultChangingOnPass(''))
        yield put(finishingFetching())
    } catch (e) {
        yield put(resultChangingNewPass(e.response.data.message))
    }
}

export default function* watchUserSettings() {
    yield takeEvery(CHANGE_NICK_NAME, changeNickNames);
    yield takeEvery(GET_USER_SETTINGS, getUserSettings);
    yield takeEvery(CHANGE_EMAIL, changeEmail);
    yield takeEvery(GET_CHANGED_EMAIL, gotChangedEmail);
    yield takeEvery(SEND_REQUEST_ON_CHANGING_PASSWORD, sendRequestOnChangingPassword)
    yield takeEvery(SEND_CODE_FOR_CHANGE_PASSWORD, sendCodeForChangePassword)
    yield takeEvery(SEND_NEW_PASSWORD_FOR_CHANGE_PASSWORD, sendNewPassword)
}