import {
  USER_POSTS_FETCHING,
  USER_POSTS_SUCCESS,
  USER_POSTS_ERROR,
} from './UserLibrariesItems.actions';

const initialState = {
  data: [],
  fetching: false,
  error: '',
};

export const userPosts = (state = initialState, action) => {
  switch (action.type) {
    case USER_POSTS_FETCHING:
      return {
        ...state,
        fetching: true,
      };

    case USER_POSTS_SUCCESS:
      return {
        ...state,
        data: action.payload,
        fetching: false,
      };

    case USER_POSTS_ERROR:
      return {
        ...state,
        error: action.payload,
        fetching: false,
      };

    default:
      return state;
  }
};
