import api from '../../Core/api';

export const getLibrariesItemsRequest = async () => api.get('/items');
export const createLibrariesItemsRequest = async data => api.post('/items', data);
export const updateLibrariesItemsRequest = async data => api.patch('/items', data);
export const deleteLibrariesItemsRequest = async id => api.delete(`/items/?id=${id}`);
