import {useState, useCallback} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {useForm} from 'react-hook-form';
import cn from 'classnames';
import {Link} from "react-router-dom";
import {Input} from '../UI/Input/Input';
import {Button} from '../UI/Button/Button';
import {ButtonTypeStyle as bts} from '../UI/Button/Button.utils';
import {userRegistration} from '../../Models/UserAuth/userAuth.actions';
import {validateOptions} from './RegistrationForm.utils';
import {errorsHelper} from '../../utils/helpers/errorsHelper';
import {isFetching, isRegistrationError, isSuccessRegistration} from '../../Models/UserAuth/userAuth.selectors';
import {Loader} from '../Loader/Loader';
import style from './RegistrationForm.module.css';
import {ComeToEmail} from "../ComeToEmailForActivationAcc/ComeToEmail";


export const RegistrationForm = () => {
    const [login, setLogin] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const regError = useSelector(isRegistrationError)
    const successRegistration = useSelector(isSuccessRegistration)
    const {
        register,
        handleSubmit,
        formState: {errors},
    } = useForm({mode: 'onBlur'});

    const fetching = useSelector(isFetching);

    const dispatch = useDispatch();


    const changeLogin = useCallback(
        e => {
            setLogin(e.target.value);
        },
        [setLogin]
    );

    const changeEmail = useCallback(
        e => {
            setEmail(e.target.value);
        },
        [setEmail]
    );

    const changePassword = useCallback(e => {
        setPassword(e.target.value);
    }, []);


    const sendLoginRequest = data => {
        dispatch(userRegistration(data));
    };

    const errorsData = errorsHelper(errors, validateOptions, style);

    if (successRegistration) {
        return <ComeToEmail/>
    }
    return (
        <div>
            {fetching && <Loader/>}
            <div className={style.containerSign}>
                <p className={style.activePageSign}>Регистрация</p>
                <p className={style.noneActivePageSign}>&nbsp;/&nbsp;</p>
                <p className={style.noneActivePageSign}>
                    <Link to='/login'>Авторизация </Link></p>
            </div>
            <form className={style.loginForm} onSubmit={handleSubmit(sendLoginRequest)}>
                <p className={style.title}>Регистрация</p>
                <p className={style.sign}>Привет,познакомимся?</p>
                <Input
                    maxLength={18}
                    value={login}
                    placeholder="Имя"
                    className={cn(style.input, style.inputTwentyEight, errorsData['username'])}
                    onChange={changeLogin}
                    register={register}
                    name="username"
                    validateOption={validateOptions.username}
                />
                {!errorsData['username'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['username']}

                <Input
                    value={email}
                    placeholder="Почта"
                    className={cn(style.input, errorsData['email'])}
                    onChange={changeEmail}
                    name="email"
                    register={register}
                    validateOption={validateOptions.email}
                />
                {!errorsData['email'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['email']}
                <Input
                    maxLength={26}
                    value={password}
                    type="password"
                    placeholder="Пароль"
                    className={cn(style.input, errorsData['password'])}
                    onChange={changePassword}
                    register={register}
                    name="password"
                    validateOption={validateOptions.password}
                />
                {!errorsData['password'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['password']}

                <Button
                    typeStyle={bts.SUCCESS}
                    text="Зарегистрироватся"
                    submit/>

                {regError.length > 0 && <p className={style.errorText}>{regError}</p>}
            </form>
        </div>
    );
};
