import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import style from './Settings.module.css'
import {Settings} from "../../components/Settings/Settings";

import {settingsInfo} from "../../Models/Settings/settings.actions";
import {settingsEmail, settingsNickName} from "../../Models/Settings/settings.selectors";
import {Loader} from "../../components/Loader/Loader";

export const SettingsPage = () => {
    const dispatch = useDispatch();
    const nickname = useSelector(settingsNickName)
    const email = useSelector(settingsEmail)
    useEffect(() => {
        dispatch(settingsInfo())
    }, [])
    if(!nickname){
        return <Loader/>
    }
    return (
        <div className="authContainer">
            <div className={style.container}>
                <Settings nickname={nickname} email={email}/>
            </div>
        </div>
    );
};
