export const CHANGE_NICK_NAME = 'CHANGE_NICK_NAME'
export const CHANGE_NICK_NAME_ERROR = 'CHANGE_NICK_NAME_ERROR'
export const GET_USER_SETTINGS = 'GET_USER_SETTINGS'
export const CHANGE_NICK_NAME_SUCCESS = 'CHANGE_NICK_NAME_SUCCESS'
export const PUT_ALL_SETTINGS = 'PUT_ALL_SETTINGS'
export const PUT_EMAIL_SUCCESS = 'CHANGE_EMAIL_SUCCESS'
export const CHANGE_EMAIL = 'CHANGE_EMAIL'
export const PUTTING_EMAIL_ERRORS = 'PUTTING_EMAIL_ERRORS'
export const FETCHING = 'FETCHING'
export const FINISHING_FETCHING = 'FINISHING_FETCHING'
export const SUCCESS_CHANGING_EMAIL = 'SUCCESS_CHANGING_EMAIL'
export const SEND_REQUEST_ON_CHANGING_PASSWORD = 'SEND_REQUEST_ON_CHANGING_PASSWORD'
export const RESULT_CHANGING_ON_PASS = 'RESULT_CHANGING_ON_PASS'
export const EMAIL_TO_CHANGE_PASSWORD = 'EMAIL_TO_CHANGE_PASSWORD'
export const SEND_CODE_FOR_CHANGE_PASSWORD = 'SEND_CODE_FOR_CHANGE_PASSWORD'
export const PUT_ERRORS_AT_CONFIRM_CODE = 'PUT_ERRORS_AT_CONFIRM_CODE'
export const SEND_NEW_PASSWORD_FOR_CHANGE_PASSWORD = 'SEND_NEW_PASSWORD_FOR_CHANGE_PASSWORD'
export const RESULT_CHANGING_NEW_PASS = 'RESULT_CHANGING_NEW_PASS'
export const RESULT_SENDING_CODE_FOR_CHANGE_PASSWORD = 'RESULT_SENDING_CODE_FOR_CHANGE_PASSWORD'
export const fetching = () => ({
    type: FETCHING
})
export const resultChangingNewPass = data => ({
    type: RESULT_CHANGING_NEW_PASS,
    payload: data
})
export const sendNewPasswordForChangePassword = data => ({
    type: SEND_NEW_PASSWORD_FOR_CHANGE_PASSWORD,
    payload: data
})
export const putErrorsAtConfirmCode = data => ({
    type: PUT_ERRORS_AT_CONFIRM_CODE,
    payload: data
})
export const resultSendingCodeForChangePassword = data => ({
    type: RESULT_SENDING_CODE_FOR_CHANGE_PASSWORD,
    payload: data
})
export const sendCodeForChangePassword = data => ({
    type: SEND_CODE_FOR_CHANGE_PASSWORD,
    payload: data
})
export const emailToChangePassword = data => ({
    type: EMAIL_TO_CHANGE_PASSWORD,
    payload: data
})
export const finishingFetching = () => ({
    type: FINISHING_FETCHING
})
export const changeEmailHook = data => ({
    type: CHANGE_EMAIL,
    payload: data
})
export const putEmail = data => ({
    type: PUT_EMAIL_SUCCESS,
    payload: data
})
export const puttingEmailErrors = data => ({
    type: PUTTING_EMAIL_ERRORS,
    payload: data
})

export const changeNickName = data => ({
    type: CHANGE_NICK_NAME,
    payload: data,
});
export const putAllSettings = data => ({
    type: PUT_ALL_SETTINGS,
    payload: data
})
export const settingsInfo = () => ({
    type: GET_USER_SETTINGS,
})
export const changeNickNameSuccess = data => ({
    type: CHANGE_NICK_NAME_SUCCESS,
    payload: data,
})
export const changeNickNameErrors = data => ({
    type: CHANGE_NICK_NAME_ERROR,
    payload: data
});
export const successChangingEmail = () => ({
    type: SUCCESS_CHANGING_EMAIL,
})
export const resultChangingOnPass = data => ({
    type: RESULT_CHANGING_ON_PASS,
    payload: data
})
export const sendEmail = data => ({
    type: SEND_REQUEST_ON_CHANGING_PASSWORD,
    payload: data
})