import {memo} from 'react';
import pt from 'prop-types';
import cn from 'classnames';
import style from './Input.module.css';
/* eslint-disable */

export const Input = memo(
    ({
        pattern,
        max,
        onKeyDown,
         maxLength,
         type,
         placeholder,
         onChange,
         className,
         register,
         name,
         validateOption,
         disabled,
         typeStyle,
         value,
         onInput,
     }) => {
        return (
            <input
                pattern={pattern}
                max={max}
                onKeyDown={onKeyDown}
                maxLength={maxLength}
                {...register(name, validateOption)}
                autoComplete="new-password"
                type={type}
                className={cn(style.input, style[typeStyle], className)}
                placeholder={placeholder}
                onChange={onChange}
                disabled={disabled}
                value={value}
                onInput={onInput}
            />
        );
    }
);

Input.propTypes = {
    maxLength:pt.number,
    type: pt.string,
    placeholder: pt.string,
    className: pt.string,
    onChange: pt.func,
    register: pt.func,
    name: pt.string,
    validateOption: pt.object,
    typeStyle: pt.string,
    disabled: pt.bool,
    value: pt.string,
};

Input.defaultProps = {
    type: 'input',
    placeholder: '',
    className: '',
    onChange: () => {
    },
    name: '',
    validateOption: {},
    typeStyle: '',
    register: () => {
    },
    disabled: false,
    value: '',
};
