import {
  USER_LOGOUTED,
  USER_LOGIN_FETCHING,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_ERROR,
  USER_REGISTRATION_ERROR,
  USER_REGISTRATION_SUCCESS,
  ACTIVATION_ACCOUNT_ERROR
} from './userAuth.actions';
import {dictionaryWithTranslatedErrors} from "../../utils/dictionary/translater.errors";

const initialState = {
  isAuth: false,
  fetching: false,
  isLoginError: '',
  isRegistrationError:'',
  successRegistration:false,
  activationAccountErrors:''
};

export const userAuth = (state = initialState, action) => {
  switch (action.type) {
    case USER_LOGOUTED:
      return {
        ...state,
        isAuth: false,
        fetching: false,
      };
    case USER_REGISTRATION_SUCCESS:
      return {
        ...state,
        fetching: false,
        successRegistration:true
      }
    case USER_LOGIN_FETCHING:
      return {
        ...state,
        fetching: true,
      };
    case USER_REGISTRATION_ERROR:
      return {
        ...state,
        isRegistrationError:dictionaryWithTranslatedErrors[action.payload],
        fetching: false,
      }
    case USER_LOGIN_SUCCESS:
      return {
        ...state,
        isAuth: true,
        fetching: false,
      };

    case USER_LOGIN_ERROR:
      return {
        ...state,
        isLoginError:dictionaryWithTranslatedErrors[action.payload],
        fetching: false,
      };
    case ACTIVATION_ACCOUNT_ERROR:
      return {
        ...state,
        activationAccountErrors:dictionaryWithTranslatedErrors[action.payload],
        fetching: false
      }
    default:
      return state;
  }
};
