import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { AuthUserRoute } from '../Routes/AuthUserRoute';
import { UnauthUserRoute } from '../Routes/UnauthUserRoute';
import { isAuth } from '../Models/UserAuth/userAuth.selectors';
import {
  userLoginSuccess,
  userLogouted,
} from '../Models/UserAuth/userAuth.actions';
import { getCookie } from '../utils/cookie';

export const App = () => {
  const isAuthValue = useSelector(isAuth);
  const refreshToken = getCookie('refreshToken');

  const dispatch = useDispatch();

  useEffect(() => {
    if (refreshToken) {
      dispatch(userLoginSuccess());
    } else {
      dispatch(userLogouted());
    }
  }, []);

  return (
    <div className="App">
      {isAuthValue ? <AuthUserRoute /> : <UnauthUserRoute />}
    </div>
  );
};
