/* eslint-disable */
import { useState, useRef } from 'react';
import { resizeFile } from './ImagePicker.utils';
import style from './ImagePicker.module.css';
import cn from 'classnames';

export const ImagePicker = ({
  imageData = '',
  isDisabled = false,
  takeImage = () => {},
  typeStyle,
  config = {},
}) => {
  const [image, setImage] = useState(imageData);
  const inputRef = useRef();

  const imageHelper = async event => {
    const file = await resizeFile(event.target.files[0], config);
    setImage(file);
    takeImage(file);
  };

  const connectOnClick = event => {
    if (isDisabled) return;
    event.preventDefault();
    inputRef.current.click();
  };

  return (
    <div>
      <input
        ref={inputRef}
        type="file"
        name="image"
        className={style.btnHidden}
        onChange={imageHelper}
        accept="image/*"
      />
      <img
        className={cn(style[typeStyle])}
        src={
          image ||
          'https://catherineasquithgallery.com/uploads/posts/2021-02/1612462713_1-p-fon-serogo-tsveta-kartinki-1.jpg'
        }
        onClick={connectOnClick}
        alt="Вставьте картинку"
        role="presentation"
      />
    </div>
  );
};
