import api from "../../Core/api";

// eslint-disable-next-line no-return-await,no-undef
export const sendChangingNickName = async data => await api.patch('/settings/change/nickname', data);
// eslint-disable-next-line no-return-await
export const getSettings = async () => await api.get('/settings')
// eslint-disable-next-line no-return-await
export const sendRequestOnChangingEmail = async data => await api.patch('/settings/change/email',data)
// eslint-disable-next-line no-return-await
export const getChangeEmail = async data => await api.get(`/settings/change/email/${data}`)
// eslint-disable-next-line no-return-await
export const requestChangeOnPass = async data => await api.patch('/settings/request/forgot/password',data)
// eslint-disable-next-line no-return-await
export const sendingCodeForChangePassword =  async data => await api.patch('/settings/sending/forgot/password',data)
// eslint-disable-next-line no-return-await
export const sendingNewPassword = async data => await api.patch('/settings/changing/forgot/password',data)