/* eslint-disable */
import axios from 'axios';
import {userLogouted} from '../Models/UserAuth/userAuth.actions';
import store from '../Core/store';
import {getCookie, setCookie, deleteCookie} from '../utils/cookie';

const api = axios.create({
    baseURL: process.env.REACT_APP_BASE_URL,
    headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
    },
});

api.interceptors.request.use((config) => {
    config.headers['Authorization'] = getCookie('accessToken')
    return config
})

api.interceptors.response.use((config) => {return config}, async function (error) {
    const originalRequest = error.config
    if (error.response.status === 401 && error.config && !error?.config._isRetry) {
        originalRequest._isRetry = true
        try {
            const sentRefreshToken = await api.post('auth/refresh', {refreshToken: getCookie('refreshToken')})
            setCookie('accessToken', sentRefreshToken.data.tokens.accessToken)
            setCookie('refreshToken', sentRefreshToken.data.tokens.refreshToken)
            originalRequest.headers['Authorization'] = getCookie('accessToken')
            return api.request(originalRequest)
        } catch (e) {
            return e
        }
    }
    throw error
})

export default api;
