/* eslint-disable */
import {Link, useNavigate} from 'react-router-dom';
import {Button} from '../UI/Button/Button';
import {useDispatch} from 'react-redux';
import {userLogout} from '../../Models/UserAuth/userAuth.actions';
import {ButtonTypeStyle as bts} from '../UI/Button/Button.utils';
import cn from 'classnames';
import style from './Header.module.css';
import testImg from '../../__mock__/img/testAvatar.jpg';
import {usePopup} from '../../hooks/usePopup';

export const Header = () => {
    const {popupRef, targetRef, isOpen, closePopup} = usePopup();
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const btnOutHandler = () => {
        closePopup();
        dispatch(userLogout());
        navigate('/login')
    };

    return (
        <div className={style.header}>
            <ul>
                <li className={style.firstEl}>
                    <Link className={style.element} to={'/'}>библиотека</Link>
                </li>
                <li>
                    <Link className={style.element} to={'/player'}>аниме</Link>
                </li>
                <li>
                    <Link className={style.element} to={'/store'}>магазин</Link>
                </li>
                <li>
                    <Link className={style.element} to={'/chat'}>чат</Link>
                </li>
                <li>
                    <Link className={style.element} to={'/news'}>новости</Link>
                </li>
            </ul>

            <div className={style.image}>
                <img role="presentation" src={testImg} alt="" ref={targetRef}/>
                <div
                    className={cn(style.popup, {[style[`popup-active`]]: isOpen})}
                    ref={popupRef}
                >
                    <Link to={'/profile'} onClick={closePopup}>
                        Профиль
                    </Link>
                    <Link to={'/settings'} onClick={closePopup}>
                        Настройки
                    </Link>
                    <Button
                        className={style.popup__btnOut}
                        typeStyle={bts.CANCEL}
                        text="Выход"
                        onClick={btnOutHandler}
                    />
                </div>
            </div>
        </div>
    );
};
