/* eslint-disable */
import {useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {useForm} from 'react-hook-form';
import {Button} from '../UI/Button/Button';
import {ButtonTypeStyle as btn} from '../UI/Button/Button.utils';
import {ImagePickerTypeStyle as img} from '../UI/ImagePicker/ImagePicker.utils';
import {Input} from '../UI/Input/Input';
import {InputTypeStyle as its} from '../UI/Input/Input.utils';
import {TextArea} from '../UI/TextArea/TextArea';
import {libraryCardState, radioButtonValue, radioButtonValues} from './LibraryCard.utils';
import {isFetching} from '../../Models/UserLibrariesItems/UserLibrariesItems.selectors';
import {Loader} from '../Loader/Loader';
import {
    createUserPostRequest,
    deleteUserPostRequest,
    updateUserPostRequest,
} from '../../Models/UserLibrariesItems/UserLibrariesItems.actions';
import style from './LibraryCard.module.css';
import {RadioButton} from '../UI/RadioButton/RadioButton';
import {
    radioButtonValue as rbv,
    defaultWatchStatus,
} from './LibraryCard.utils';
import cn from 'classnames';
import {ImagePicker} from '../UI/ImagePicker/ImagePicker';

export const LibraryCard = ({data, closeCard}) => {

    const watchStatus = data => {
        return {
            [rbv.WILL_WATCH]: data?.watchStatus === rbv.WILL_WATCH,
            [rbv.WATCH]: data?.watchStatus === rbv.WATCH,
            [rbv.WATCHED]: data?.watchStatus === rbv.WATCHED,
            [rbv.ABANDONED]: data?.watchStatus === rbv.ABANDONED,
        }
    }

    const [radioValue, setRadioValue] = useState(
        watchStatus(data) || defaultWatchStatus
    );
    const [title, setTitle] = useState(data?.title || '');
    const [season, setSeason] = useState(data?.season || '');
    const [typeInfo, setTypeInfo] = useState(data?.typeInfo || '');
    const [genre, setGenre] = useState(data?.genre || '');
    const [studio, setStudio] = useState(data?.studio || '');
    const [author, setAuthor] = useState(data?.author || '');
    const [description, setDescription] = useState(data?.description || '');
    const [image, setImage] = useState(data?.image || '');
    const [cardState, setCardState] = useState(
        data ? libraryCardState.IS_VIEW : libraryCardState.IS_CREATE
    );
    const [favourite, setFavourite] = useState(data?.favourite || false);

    const dispatch = useDispatch();
    const fetching = useSelector(isFetching);

    const {register, handleSubmit} = useForm({mode: 'onBlur'});


    const conversionToOneWordStatus = (radioValue) => {
        let newValueOfWatchStatus = ''
        Object.entries(radioValue).forEach(([key, value]) => {
            if (value === true) {
                newValueOfWatchStatus = key
            }
        });

        return newValueOfWatchStatus
    }

    const createPost = dataRequest => {

        dispatch(
            createUserPostRequest({
                ...dataRequest,
                image,
                genre:[ {
                    "id": 1,
                    "genreName": "Порно"
                }],
                year:2000,
                watchStatus: conversionToOneWordStatus(radioValue),
                favourite: favourite,
            })
        );
        closeCard(false);
    };

    const updatePost = dataRequest => {
        dataRequest.id = data.id;
        dispatch(
            updateUserPostRequest({
                ...dataRequest,
                image, genre:[ {
                    "id": 1,
                    "genreName": "Порно"
                }],
                year:2000,
                watchStatus: conversionToOneWordStatus(radioValue),
                favourite: favourite,
            })
        );
        setCardState(libraryCardState.IS_VIEW);
    };

    const deletePost = () => {
        dispatch(deleteUserPostRequest(data.id));
        closeCard(false);
    };

    const isCreate = cardState === libraryCardState.IS_CREATE;
    const isView = cardState === libraryCardState.IS_VIEW;
    const isEdit = cardState === libraryCardState.IS_EDIT;
    const isDisabled = isView;
    const textSubmitButton = isCreate ? 'Добавить' : 'Сохранить';

    const sendCard = dataReq => {
        if (isCreate) {
            createPost(dataReq);
        } else {
            updatePost(dataReq);
        }
    };

    const radioChange = v => {
        setRadioValue({
            [rbv.WILL_WATCH]: v === rbv.WILL_WATCH,
            [rbv.WATCH]: v === rbv.WATCH,
            [rbv.WATCHED]: v === rbv.WATCHED,
            [rbv.ABANDONED]: v === rbv.ABANDONED,
        });
    };

    return (
        <form className={style.card} onSubmit={handleSubmit(sendCard)}>
            {fetching && <Loader/>}
            <Input
                name="title"
                value={title}
                className={style.name}
                typeStyle={its.TEXTCARD}
                onChange={e => setTitle(e.target.value)}
                placeholder="Введи название"
                disabled={isDisabled}
                register={register}
            />
            <div className={style.shell}>
                <div className={style.info}>
                    <ImagePicker
                        typeStyle={img.LABEL}
                        imageData={image}
                        takeImage={setImage}
                        isDisabled={isDisabled}
                    />
                    <Button
                        className={cn(style.btnFavourite)}
                        typeStyle={favourite ? btn.GREENSUCCESS : btn.CANCEL}
                        name="favourite"
                        value={favourite}
                        text="Избранное"
                        disabled={isDisabled}
                        onClick={() => setFavourite(!favourite)}
                    />
                </div>
                <div className={style.textBlock}>
                    <div className={style.text}>
                        <div className={style.descriptionElement}>
                            <p>Сезон:</p>
                            <Input
                                name="season"
                                value={season}
                                className={style.info}
                                typeStyle={its.TEXTCARD}
                                onChange={e => setSeason(e.target.value)}
                                placeholder="Введи Сезон"
                                disabled={isDisabled}
                                register={register}
                            />
                        </div>
                        <div className={style.descriptionElement}>
                            <p>Тип:</p>
                            <Input
                                name="typeInfo"
                                value={typeInfo}
                                className={style.info}
                                typeStyle={its.TEXTCARD}
                                onChange={e => setTypeInfo(e.target.value)}
                                placeholder="Введи Тип"
                                disabled={isDisabled}
                                register={register}
                            />
                        </div>
                        <div className={style.descriptionElement}>
                            <p>Жанр: </p>
                            <Input
                                name="genre"
                                value={genre}
                                className={style.info}
                                typeStyle={its.TEXTCARD}
                                onChange={e => setGenre(e.target.value)}
                                placeholder="Введи Жанр"
                                disabled={isDisabled}
                                register={register}
                            />
                        </div>
                        <div className={style.descriptionElement}>
                            <p>Студия: </p>
                            <Input
                                name="studio"
                                value={studio}
                                className={style.info}
                                typeStyle={its.TEXTCARD}
                                onChange={e => setStudio(e.target.value)}
                                placeholder="Введи Студию"
                                disabled={isDisabled}
                                register={register}
                            />
                        </div>
                        <div className={style.descriptionElement}>
                            <p>Автор: </p>
                            <Input
                                className={style.info}
                                register={register}
                                value={author}
                                name="author"
                                typeStyle={its.TEXTCARD}
                                onChange={e => setAuthor(e.target.value)}
                                placeholder="Введи Автора"
                                disabled={isDisabled}
                            />
                        </div>
                        <hr/>
                        <TextArea
                            className={style.info2}
                            register={register}
                            value={description}
                            name="description"
                            type="textarea"
                            wrap="hard"
                            rows="20"
                            cols="50"
                            onChange={e => setDescription(e.target.value)}
                            placeholder="Введи описание аниме"
                            disabled={isDisabled}
                        />
                    </div>
                    <div className={style.btnGroup}>
                        {(isCreate || isEdit) && (
                            <Button
                                className={style.btnAdd}
                                typeStyle={btn.GREENSUCCESS}
                                text={textSubmitButton}
                                submit
                            />
                        )}
                        {isView && (
                            <Button
                                className={style.btnAdd}
                                typeStyle={btn.SUCCESS}
                                text="Редактировать"
                                onClick={() => setCardState(libraryCardState.IS_EDIT)}
                            />
                        )}
                        {isEdit && (
                            <Button
                                className={style.btnAdd}
                                typeStyle={btn.CANCEL}
                                text="Удалить"
                                onClick={deletePost}
                            />
                        )}
                    </div>
                </div>
            </div>

            <hr/>
            <div className={style.interactive}>
                <div>
                    <RadioButton
                        name={rbv.WILL_WATCH}
                        label="Буду смотреть"
                        value={radioValue[rbv.WILL_WATCH]}
                        disabled={isDisabled}
                        onChange={radioChange.bind(this, rbv.WILL_WATCH)}
                    />
                </div>
                <div>
                    <RadioButton
                        name={rbv.WATCH}
                        label="Смотрю"
                        value={radioValue[rbv.WATCH]}
                        disabled={isDisabled}
                        onChange={radioChange.bind(this, rbv.WATCH)}
                    />
                </div>
                <div>
                    <RadioButton
                        name={rbv.WATCHED}
                        value={radioValue[rbv.WATCHED]}
                        label="Просмотрено"
                        disabled={isDisabled}
                        onChange={radioChange.bind(this, rbv.WATCHED)}
                    />
                </div>
                <div>
                    <RadioButton
                        name={rbv.ABANDONED}
                        value={radioValue[rbv.ABANDONED]}
                        label="Брошено"
                        disabled={isDisabled}
                        onChange={radioChange.bind(this, rbv.ABANDONED)}
                    />
                </div>
            </div>
        </form>
    );
};
