import {useState, useCallback} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import {useForm} from 'react-hook-form';
import cn from 'classnames';
import {Input} from '../UI/Input/Input';
import {Button} from '../UI/Button/Button';
import {userLogin} from '../../Models/UserAuth/userAuth.actions';
import {validateOptions} from './LoginForm.utils';
import {errorsHelper} from '../../utils/helpers/errorsHelper';
import {isFetching, isLoginError} from '../../Models/UserAuth/userAuth.selectors';
import {Loader} from '../Loader/Loader';
import style from './LoginForm.module.css';

export const LoginForm = () => {
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const loginError = useSelector(isLoginError)
    const fetching = useSelector(isFetching);
    const dispatch = useDispatch();


    const {
        register,
        handleSubmit,
        formState: {errors},
    } = useForm({mode: 'onChange'});

    const changeLogin = useCallback(
        e => {
            setLogin(e.target.value);
        },
        [setLogin]
    );

    const changePassword = useCallback(
        e => {
            setPassword(e.target.value);
        },
        [setPassword]
    );


    const sendLoginRequest = data => {
        dispatch(userLogin({login: data.username, password: data.password}));
    };
    const errorsData = errorsHelper(errors, validateOptions, style);

    return (
        <div>
            {fetching && <Loader/>}
            <div className={style.containerSign}>
                <p className={style.noneActivePageSign}>
                    <Link to='/registration'>Регистрация</Link></p>
                <p className={style.noneActivePageSign}>&nbsp;/&nbsp;</p>
                <p className={style.activePageSign}>Авторизация</p>
            </div>
            <form
                className={style.loginForm}
                onSubmit={handleSubmit(sendLoginRequest)}>
                <p className={style.title}>Авторизация</p>
                <p className={style.sign}>Привет,я тебя знаю?</p>
                <Input
                    name="username"
                    register={register}
                    value={login}
                    placeholder="Логин"
                    className={cn(style.inputs, errorsData['username'])}
                    onChange={changeLogin}
                    validateOption={validateOptions.username}
                />
                {!errorsData['username'] ? <p className={style.errorText}>&nbsp;</p> : errorsData['username']}

                <Input
                    maxLength={26}
                    name="password"
                    register={register}
                    value={password}
                    type="password"
                    placeholder="Пароль"
                    className={cn(style.inputs, errorsData['password'])}
                    onChange={changePassword}
                    validateOption={validateOptions.password}
                />
                {/* eslint-disable-next-line no-nested-ternary */}
                {!errorsData['password'] ? ((loginError.length > 0) ? <p className={style.errorText}>{loginError}</p> :
                    <p className={style.errorText}>&nbsp;</p>) : errorsData['password']}


                <Button
                    className={style.successBtn}
                    text="Войти"
                    submit/>
                <div className={style.container}>
                    <Link className={style.forgotPassword} to="/forgot/password">Забыл пароль?</Link>
                </div>
            </form>
        </div>
    );
};
