import {Routes, Route} from 'react-router-dom';
import {AnimLibrary} from '../Pages/AnimLibrary/AnimLibrary';
import {ProfilePage} from '../Pages/ProfilePage/ProfilePage';
import {Header} from '../components/Header/Header';
import {NotFound} from '../components/NotFound/NotFound';

import {SettingsPage} from "../Pages/SettingsPage/SettingsPage";
import {ChangingEmail} from "../Pages/ChangingEmail/ChangingEmail";


export const AuthUserRoute = () => {
    return (
        <div className="container">
            <Header/>
            <Routes>
                <Route path="/settings" element={<SettingsPage/>}/>
                <Route path="/" element={<AnimLibrary/>}/>
                <Route path='/settings/change/email/*' element={<ChangingEmail/>}/>
                <Route path="/profile" element={<ProfilePage/>}/>
                <Route path="*" element={<NotFound/>}/>
            </Routes>
        </div>
    );
};
